body {
  /* align-items: center; */
  display: flex;
  justify-content: center;
  height: 100vh;
  /* background: rgb(0,255,190); */
  /* background: linear-gradient(180deg, rgba(0,255,190,1) 0%, rgba(0,14,148,1) 64%, rgba(144,0,142,1) 100%); */
}

h1 {
  color: #6933ff !important;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  text-align: center;
}

.chest-util {
  max-width: 400px;
  text-align: center;
  border-radius: 20px;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 20px;
  box-shadow: 0px 0px 5px black;
  /* background: rgba(220,220,220,0.8); */
  /* border-style: none; */
}

code {
  background-color: #eee;
  border: 1px solid #999;
  display: block;
  padding: 20px;
}

.submission-boxes {
  text-align: center;
}

.try-it {
  text-align: right;
}

input[type="submit"] {
  border: none;
  background-color: #6933ff;
  border-radius: 100px;
  color: white;
  font-size: large;
  margin-top: 5px;
  padding: 7px;
  width: 100px;
}

button {
  border: none;
  background-color: #6933ff;
  border-radius: 100px;
  color: white;
  font-size: large;
  margin-top: 5px;
  padding: 7px;
  width: 100px;
}

p {
  text-align: left;
}

.example {
  margin-bottom: 10px;
}

.ty {
  position: absolute;
  bottom: 0;
  right: 0;
  }